.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0 16px;
  padding-bottom: env(safe-area-inset-bottom);
}

/* Header */
.app-header {
  text-align: center;
  padding: 40px 0 24px;
  animation: fadeInDown 0.6s ease-out;
}

.header-decoration {
  font-size: 2rem;
  margin-bottom: 8px;
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

.app-title {
  font-size: 1.75rem;
  font-weight: 800;
  margin-bottom: 8px;
  letter-spacing: -0.5px;
}

.name-highlight {
  background: linear-gradient(135deg, #ff6b9d 0%, #c44569 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.app-subtitle {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
}

/* Main */
.app-main {
  flex: 1;
  padding-bottom: 24px;
}

/* Footer */
.app-footer {
  text-align: center;
  padding: 24px 0;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.875rem;
  font-weight: 600;
}

/* Animations */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
